<template>
  <div>
    <el-form ref="form" :model="form" label-width="80px">
      <el-form-item :label="$t('imageContentSimpleAdd_registry')" prop="imageUrl">
<!--        注册表-->
        <el-select v-model="form.imageUrl" :placeholder="$t('imageContentSimpleAdd_Pleaseselectsource')">
          <el-option label="DockerHub" value="DockerHub"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item
      label="标签名称"
      prop="imageName"
      :rules="[
        { required: true, message: '标签名称不能为空'}
      ]"
      >
        <el-input v-model.trim="form.imageName" :placeholder="$t('imageContentSimpleAdd_forexample')"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button style="background: #067FB5; color: #fff;" size="small" @click="onSubmit()">{{$t('imageContentSimpleAdd_Submit') }}</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import http from '@/utils/http'
export default {
  data () {
    return {
      form: {
        imageUrl: 'DockerHub',
        imageName: ''
      }
    }
  },
  props: ['imageId'],
  methods: {
    // 处理提交事件
    onSubmit () {
      // 判断镜像是否为空
      if (this.form.imageName === '') {
        return this.$message({
          showClose: true,
          message: '标签名称为空',
          type: 'error'
        })
      }
      // ajax请求
      const docker = `curl --unix-socket /var/run/docker.sock -s -X POST "http:/v1.40/images/${this.imageId}/tag?repo=${this.form.imageName}"` // 标记镜像
      http({
        url: '/docker',
        method: 'POST',
        timeout: 600000000,
        data: { docker }
      }).then(res => {
        if (res.Code !== 200) {
          return this.$message({
            showClose: true,
            message: '标签添加失败！',
            type: 'error'
          })
        }
        this.$message({
          showClose: true,
          message: '标签添加成功！',
          type: 'success'
        })
        this.$emit('success')
        this.form.imageName = ''
      })
    }
  }
}
</script>
